define('ui/utils/parse-volume', ['exports', 'ui/models/volumespec'], function (exports, _uiModelsVolumespec) {
  exports.parseVolumeSpec = parseVolumeSpec;
  exports.stringifyVolumeSpec = stringifyVolumeSpec;

  // name:/incontainer:opt,opt2
  // name:/incontainer
  // /onhost:/incontainer:opt
  // /onhost:/incontainer
  // /incontainer
  // @TODO support the windows clown-show (https://github.com/moby/moby/blob/master/volume/volume_windows.go)

  function parseVolumeSpec(str) {
    str = str.trim();

    var source = '',
        dest = '',
        opts = '';

    var parts = str.split(':');
    switch (parts.length) {
      case 1:
        dest = parts[0];
        break;
      case 2:
        source = parts[0];
        dest = parts[1];
        break;
      case 3:
        source = parts[0];
        dest = parts[1];
        opts = parts[2];
        break;
      default:
        return null;
    }

    if (!opts) {
      opts = 'rw';
    }

    return _uiModelsVolumespec['default'].create({
      source: source,
      dest: dest,
      opts: opts
    });
  }

  function stringifyVolumeSpec(spec) {
    var source = (spec.source || '').trim();
    var dest = (spec.dest || '').trim();
    var opts = (spec.opts || '').trim();

    // RW is the default
    if (opts === 'rw') {
      opts = '';
    }

    if (opts) {
      return source + ':' + dest + ':' + opts;
    } else if (source) {
      return source + ':' + dest;
    } else {
      return dest;
    }
  }

  exports['default'] = {
    parseVolumeSpec: parseVolumeSpec,
    stringifyVolumeSpec: stringifyVolumeSpec
  };
});